<template>
  <div class="product">
    <v-app id="productList">
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="products"
        :single-select="false"
        item-key="name"
        class="elevation-1"
      >
      </v-data-table>
    </v-app>
  </div>
</template>

<script>
import ProductService from '../../services/ProductService'

export default {
  data() {
    return {
      products: [],
      headers: [
        {text: 'Item Code', value: 'productId'},
        {text: 'Name', value: 'name'},
        {text: 'Brand', value: 'brand'},
        {text: 'Details', value: 'other_details'},
        {text: 'Vehicle', value: 'for_vehicle'},
        {text: 'Color', value: 'color'},
        {text: 'Aqui Price', value: 'aqui_price'},
        {text: 'SRP', value: 'srp'},
        {text: 'Stock Level', value: 'stock_level'},
        {text: 'Category Id', value: 'categoryId'},
        {text: 'Supplier Name', value: 'supplier_name'}
      ],

    }
  },
  computed() {

  },
  async created() {
    try {
      this.products = await ProductService.getProducts();
    } catch (err) {
      this.error = err.message;
    }
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');

#orderList {
  font-family: 'Raleway', sans-serif;
}
</style>